.i{
    display : flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    align-items: center;
    justify-content: center;
    display: flex;
    position: relative;
    background-color: skyblue;
    
    


}

.i-right{
    flex: 1;
    position: relative;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 30px;
    font-weight: 300px;
}
.i-name{

    font-size:45px;

}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
    height: 100%;
    animation : move 10s ease-in-out infinite alternate;
    
}

@keyframes move{

    25%{
        transform: translateY(-50px);

    }

    50%{

        transform: translateY(-100px);

    }

    75%{
        transform: translateY(-150px);
    }

    100%{
        transform: translateY(-200px);
    }



    
}

.i-title-item{
    height :50px;
    font-size :30px;
    font-weight: bold;
    color: green;
    display: flex;
    align-items: center;
}



.i-image{
    width: 100%;
    height : 100%;
    object-fit: cover;
    position: absolute;
}

.i-scroll{
    width : 30px;
    height: 30px;
    position: absolute;
    bottom: 20px;
}

@media  screen and (max-width:480px) {

    .i{
        flex-direction: column;
    }

    .i-left-wrapper{

        padding: 10px;
        align-items: center;
        text-align: center;
        justify-content: center;
        height: 100%;

    }
    
}



